/* HEAD / NAV */
body {
  font-family: sans-serif; }

a {
  text-decoration: none; }

label {
  display: block; }

nav a {
  display: inline-block;
  margin: 1em; }

.navbar-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 0 42px;
  padding-top: 8px; }

.logo-container {
  /* margin: 0 auto; */
  display: flex;
  align-items: center;
  justify-content: center; }

.img-logo {
  width: 40px; }

.txt-logo {
  margin: 8px 6px 0 6px;
  font-size: 1.5em;
  font-weight: bold;
  text-align: center; }

/* later - after login/logout available */
/* .nav-menu-main {
  width: 58%;
} */
.nav-menu-main {
  width: fit-content;
  float: right; }

.nav-menu-main .ui.secondary.menu .active.item {
  background: transparent;
  color: #a333c8;
  border-radius: 6px;
  border: 1px solid transparent;
  border-bottom-color: #a333c8; }

.ui.secondary.menu .dropdown.item:hover,
.ui.secondary.menu .link.item:hover,
.ui.secondary.menu a.item:hover {
  background: transparent;
  color: #a333c8; }

/* MAIN CONTENT */
/* Melody */
.melody-main-wrapper {
  margin: 25px auto;
  width: fit-content; }

.melody-main-form {
  padding: 0 16px 16px 16px; }

.melody-main-form .fields {
  flex-wrap: wrap; }

.melody-main-form .field > label {
  padding-left: 0; }

.ui.form .fields {
  margin-bottom: 4px; }

/* Melody Submit Button */
/* .melody-main-form > .field:last-child {
  text-align: center;
} */
.melody-main-form .button-main-submit {
  text-align: center; }

.melody-main-form .button-main-submit button {
  display: inline-block;
  width: 150px; }

.melody-result-wrapper {
  width: 100%; }

.melody-result-single {
  width: fit-content;
  min-width: 320px;
  margin: 0 auto;
  margin-bottom: 16px; }

.melody-result-single > .ui.table {
  margin-bottom: 4px; }

.melody-preview-modal {
  text-align: center;
  font-weight: bold; }

.melody-preview-modal-progress-label {
  margin-bottom: 6px; }

.melody-preview-modal-tempo-label {
  margin: 6px 0 -12px 0; }

.melody-preview-modal .ui.table td.active, .melody-preview-modal .ui.table tr.active {
  background: #a333c8 !important; }

