/* Rhythm */
.rhythm-container,
.rhythm-main-form {
  width: fit-content;
  margin: 0 auto;
  padding: 0 12px; }

.rhythm-main-form-tempo-field {
  width: 196px; }

/* not sold on this */
.rhythm-main-form-tempo-field-slider-container {
  border: 1px solid rgba(34, 36, 38, 0.15);
  border-radius: 4px;
  padding: 8px 2px; }

.rhythm-main-form-tempo-field-slider-container .rangeslider {
  margin: 4px; }

/* Center button */
.rhythm-main-form > .field:last-child {
  text-align: center; }

.rhythm-info-popup {
  font-size: 18px;
  padding-left: 10px;
  display: inline-block;
  vertical-align: top; }

.rhythm-output {
  margin-top: 16px; }

.rhythm-output h1 {
  text-align: center;
  margin-bottom: 16px; }

.rhythm-output div {
  margin-bottom: 16px;
  padding: 0 0 0 8px;
  font-size: 16px;
  overflow-y: scroll;
  height: 56vh; }

.rhythm-output table.ui.table {
  margin: 0 0 24px 0;
  padding: 0;
  border: none;
  border-collapse: collapse;
  font-weight: bold; }

.rhythm-output table.ui.table td {
  border-style: hidden;
  padding: 0 8px; }

.rhythm-output table.ui.table button.ui.button {
  padding: 14px !important; }

.rhythm-output table.ui.table button.ui.button > i.icon {
  width: 28px; }

.rhythm-output img {
  height: 38px; }

/* Range Slider */
.rangeslider-horizontal .rangeslider__fill {
  background-color: #a333c8; }

/* change to 950 after adding more links */
@media screen and (max-width: 767px) {
  .nav-menu-main {
    width: 100%;
    margin: 0 auto; }
  .logo-container {
    width: 100%; } }

@media only screen and (max-width: 767px) {
  .nav-menu-main .ui.stackable.menu .item {
    width: auto !important;
    /* override semantic */ } }

@media screen and (max-width: 500px) {
  .img-logo {
    width: 20px; }
  .txt-logo {
    font-size: 1.2em; } }

@media screen and (max-width: 350px) {
  .txt-logo {
    width: 56%; } }
